import { LogoIcon } from '../assets/images';
import AuthInput from '../components/Auth/AuthInput';
import { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Swal from 'sweetalert2';
import styles from './LoginPage.module.scss'

const LoginPage = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const { login, isAuthenticated } = useAuth(); // 
  const handleClick = async () => {
    if (username.length === 0 || password.length === 0) {
      Swal.fire({
        title: '',
        icon: 'error',
        showConfirmButton: false,
        timer: 1000,
        position: 'top',
      });
      return;
    }
    console.log(username, password)
    const success = await login({ username, password }); //login()  AuthContext api
    if (success) {
      Swal.fire({
        title: '',
        icon: 'success',
        showConfirmButton: false,
        timer: 1000,
        position: 'top',
      });
      return;
    }
    Swal.fire({
      title: '',
      icon: 'error',
      showConfirmButton: false,
      timer: 1000,
      position: 'top',
    });
  };
  useEffect(() => {
    // rendernavigateisAuthenticated
    if (isAuthenticated) {
      navigate('/shop');
    }
  }, [navigate, isAuthenticated]);

  return (
    <div className={styles.authContainer}>
      <div>
        <LogoIcon />
      </div>
      <h1 className={styles.loginTitle}>Alpha Shop</h1>

      <div className={styles.authInputContainer}>
        <AuthInput
          label=""
          placeholder=""
          //defaultValue={username}
          value={username}
          onChange={(nameInputValue) => setUsername(nameInputValue)}
        />
      </div>

      <div className={styles.authInputContainer}>
        <AuthInput
          label=""
          placeholder=""
          type="password"
          //defaultValue={password}
          value={password}
          onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        />
      </div>
      <button className={styles.authButton} onClick={handleClick}></button>
      <Link to="/signup">
        <div className={styles.authLinkText}></div>
      </Link>
    </div>
  );
};

export default LoginPage;