import { createContext, useContext, useState, useEffect } from 'react';
import { register, login, checkPermission, saveUser } from '../api/auth';
import * as jwt from 'jsonwebtoken';
import { useLocation } from 'react-router-dom';
import { useProducts } from './ProductsContext';

// 
const defaultAuthContext = {
  isAuthenticated: false,
  currentMember: null, //
  register: null, // authContext
  login: null,
  logout: null,
};

const AuthContext = createContext(defaultAuthContext);
// context
export const useAuth = () => useContext(AuthContext);
// providercontext
export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [payload, setPayload] = useState(null);
  const { setFilters } = useProducts();
  const { pathname } = useLocation();

  useEffect(() => {
    const checkTokenIsValid = async () => {
      const authToken = localStorage.getItem('authToken');
      if (!authToken) {
        setPayload(null);
        setIsAuthenticated(false);
        return;
      }
      const result = await checkPermission(authToken);
      if (result) {
        const tempPayload = jwt.decode(authToken);
        setPayload(tempPayload);
        setIsAuthenticated(true);
      } else {
        setPayload(null);
        setIsAuthenticated(false);
      }
    };
    checkTokenIsValid();
  }, [pathname]);

  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        currentMember: payload && { id: payload.sub, name: payload.name },
        register: async (data) => {
          const { success, authToken } = await register({
            username: data.username,
            email: data.email,
            password: data.password,
          });
          const tempPayload = jwt.decode(authToken); // tokenusername, email, password
          if (tempPayload) {
            // 
            //  save usercartContextfavoriteContext
            await saveUser(tempPayload.name);
            // currentMemberisAuthenticatesaveUserusererror
            setPayload(tempPayload);
            setIsAuthenticated(true);
            localStorage.setItem('authToken', authToken);
            return success;
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
        },
        login: async (data) => {
          const { success, authToken } = await login({
            username: data.username,
            password: data.password,
          });
          const tempPayload = jwt.decode(authToken); // tokenusername, email, password
          if (tempPayload) {
            // 
            setPayload(tempPayload);
            setIsAuthenticated(true);
            localStorage.setItem('authToken', authToken);
            // save user
            await saveUser(tempPayload.name);
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
          return success;
        },
        logout: async () => {
          setFilters({ "size": 'Size', "color": 'Color', "category": 'Category', "search": '' });
          setPayload(null);
          setIsAuthenticated(false);
          localStorage.removeItem('authToken');
        },
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};